import React, {useEffect, useState} from 'react';
import {put_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import CircularProgress from "@material-ui/core/CircularProgress";
const IsNewComponent = ({product_id, is_new}) => {
    const [state, setState] = useState({
        is_new: 0,
        isLoading : false
    })
    useEffect(()=>{
        setState({
            ...state,
            is_new: is_new,
            isLoading: false
        })
    },[is_new, product_id])
    const handleNewChange = (e)=>{
        let val = e.target.checked? 1: 0
        setState({
            ...state, isLoading: true
        })
        put_data(BACK_END_URL + `admin/product/${product_id}/new`, {}, {is_new : val}, true)
            .then(res => setState({
                isLoading: false,
                is_new: !state.is_new
            }))
            .catch(error => console.log(error))
    }
    return (
        <div>
            {
                state.isLoading?
                <CircularProgress size={20}/> :
                <input type='checkbox' name='is_new' checked={state.is_new} onChange={handleNewChange}/>
            }
        </div>
    );
};

export default IsNewComponent;