import React, {useEffect, useState} from 'react';
import {put_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import CircularProgress from "@material-ui/core/CircularProgress";
const IsHotComponent = ({product_id, is_hot}) => {
    const [state, setState] = useState({
        is_hot: 0,
        isLoading : false
    })
    useEffect(()=>{
        setState({
            ...state,
            is_hot: is_hot,
            isLoading: false
        })
    },[is_hot, product_id])
    const handleHotChange = (e)=>{
        let val = e.target.checked? 1: 0
        setState({
            ...state, isLoading: true
        })
        put_data(BACK_END_URL + `admin/product/${product_id}/hot`, {}, {is_hot : val}, true)
            .then(res => setState({
                isLoading: false,
                is_hot: !state.is_hot
            }))
            .catch(error => console.log(error))
    }
    return (
        <div>
            {
                state.isLoading?
                <CircularProgress size={20}/> :
                <input type='checkbox' name='is_hot' checked={state.is_hot} onChange={handleHotChange}/>
            }
        </div>
    );
};

export default IsHotComponent;