import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import {blue} from '@material-ui/core/colors';
import {TextField, Divider, Button} from "@material-ui/core";
import HelpIcon from '@material-ui/icons/Help';
import {post_data, put_data} from "../../ApiCall";
import {BACK_END_URL} from "../../ServiceURL";

const useStyles = makeStyles({
    avatar: {
        backgroundColor: blue[100],
        color: blue[600],
    },
    container: {
        padding: '1rem',
        minWidth: '500px',
        '&>*': {
            marginTop: '1rem'
        }
    },
    input: {
        width: '100%'
    },
    icon: {
        width: '100%'
    },
    footer: {
        display: 'flex',
        flexDirection: 'row',
        marginTop: '1rem',
        justifyContent: 'flex-end'
    }
});
export default function EditBranchDialog(props) {
    const {onClose, refresh_list, open, data} = props;
    const classes = useStyles();
    const [state, setState] = useState({
        name: null,
        address: null,
        time_work: null,
        phone: null,
        email: null,
        description: null
    })
    useEffect(() => {
        setState({
            name: data.name,
            address: data.address,
            time_work: data.time_work,
            phone: data.phone,
            email: data.email,
            description: data.description
        })
    }, [data])

    const handleAddCategory = () => {
        put_data(BACK_END_URL + `/admin/branch/${data.branch_id}`, {}, {
            name: state.name,
            address: state.address,
            time_work: state.time_work,
            phone: state.phone,
            email: state.email,
            description: state.description
        }, true)
            .then(res => {
                refresh_list()
                onClose()
            })
            .catch(error => alert('Error when edit branch. Try again later'))
    };
    const handleChange = (e) => {
        let name = e.target.name
        let val = e.target.value
        setState({
            ...state,
            [name]: val
        })
    }
    return (
        <Dialog aria-labelledby="edit-branch" open={open}>
            <DialogTitle id = "edit-branch">Add Branch</DialogTitle>
            <Divider variant={'middle'}/>
            <div className={classes.container}>
                <TextField variant={'outlined'} placeholder={'Name'} required={true}
                           name='name' value={state.name} onChange={handleChange} className={classes.input}></TextField>
                <TextField variant={'outlined'} placeholder={'Address'} required={true}
                           name='address' value={state.address} onChange={handleChange}
                           className={classes.input}></TextField>
                <TextField variant={'outlined'} placeholder={'Time Work'} required={true}
                           name='time_work' value={state.time_work} onChange={handleChange}
                           className={classes.input}></TextField>
                <TextField variant={'outlined'} placeholder={'Phone'} required={true}
                           name='phone' value={state.phone} onChange={handleChange}
                           className={classes.input}></TextField>
                <TextField variant={'outlined'} placeholder={'Email'} required={true}
                           name='email' value={state.email} onChange={handleChange}
                           className={classes.input}></TextField>
                <TextField variant={'outlined'} placeholder={'Description'} required={true} multiline={true} rows={3}
                           name='description' value={state.description} onChange={handleChange}
                           className={classes.input}></TextField>
                <div className={classes.footer}>
                    <Button variant={'contained'} color={'primary'} onClick={handleAddCategory}>Save</Button>
                    <Button style={{marginLeft: '0.5rem'}} onClick={onClose}>Cancel</Button>
                </div>
            </div>
        </Dialog>
    );
}