import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TableBranch from "../Branch/Table.Branch";
import {NavLink} from "react-router-dom";
import {IconButton, Typography, Divider, withStyles, Tooltip, Input, InputAdornment} from "@material-ui/core";
import EditIcon from "@material-ui/icons/Edit";
import RestoreFromTrashIcon from "@material-ui/icons/RestoreFromTrash";
import DeleteDialog from "../Dialog.Delete";
import SubdirectoryArrowRightRoundedIcon from '@material-ui/icons/SubdirectoryArrowRightRounded';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import {delete_data, get_data} from "../../ApiCall";
import {BACK_END_URL} from "../../ServiceURL";
import TablePagination from '@material-ui/core/TablePagination';
import CircularProgress from '@material-ui/core/CircularProgress';
import IsNewComponent from "../IsNewComponent";
import IsHotComponent from "../IsHotComponent";

const useStyles = makeStyles({
    container: {
        height: '70vh',
    },
    table: {
        minWidth: 650,
    },
    imageMain: {
        height: '3rem',
        width: '3rem'
    },
    allTable: {
        display: 'inline-block',
        marginTop: '2rem',
        marginLeft: '2rem',
        fontWeight: 'bold',
        borderBottom: '3px solid blue'
    },
    searchContainer: {
        padding: '2rem 1rem 2rem 1rem',
        display: 'flex',
        flexDirection: 'row'
    },
    search: {
        border: '1px solid black',
        // borderRadius : '1rem',
        opacity: '70%',
        float: 'right',
        width: '100%',
        '&>input': {
            // borderRadius : '0 1rem 1rem 0',
        }
    },
    selectContainer: {
        borderRight: 0,
        width: '7rem',
        '&::selection': {
            outline: 'none'
        },
        '&>*': {
            fontSize: '1rem',
            padding: '1rem'
        }
    },
    optionSelect: {},
    headerTable: {
        '&>*': {
            backgroundColor: 'white'
        }
    }
});

export const HtmlTooltip = withStyles((theme) => ({
    tooltip: {
        backgroundColor: '#f5f5f9',
        color: 'rgba(0, 0, 0, 0.87)',
        maxWidth: 220,
        fontSize: theme.typography.pxToRem(12),
        border: '1px solid #dadde9',
    },
    arrow: {
        color: "#f5f5f9"
    }
}))(Tooltip);

export default function TableProduct() {
    const classes = useStyles();
    const [state, setState] = useState({
        list_product: [],
        page: 1,
        total_page: 1,
        item_per_page: 5,
        search: '',
        total: 0,
        order: 'name,asc',
        isLoading: false
    })
    const [openDeleteDialog, setOpenDeleteDialog] = useState(false)
    const [itemDelete, setItemDelete] = useState(0)
    const handleDeleteProduct = () => {
        delete_data(BACK_END_URL + `/admin/product/${itemDelete}`,
            {}, true)
            .then(res => {
                setOpenDeleteDialog(false)
                refresh_list()
            })
    }
    const refresh_list = () => {
        setState({...state, isLoading: true})
        get_data(BACK_END_URL + '/public/all_product',
            {
                page: state.page,
                item_per_page: state.item_per_page,
                search: state.search,
                order: state.order
            }, false)
            .then(res => setState({
                ...state,
                item_per_page: res.data.item_per_page,
                list_product: res.data.list,
                page: res.data.page,
                total_page: res.data.total_page,
                total: res.data.total,
                isLoading: false
            }))
    }
    const handleKeyDown = (e) => {
        if (e.key === 'Enter') {
            setState({...state, isLoading: true})
            get_data(BACK_END_URL + '/public/all_product',
                {
                    page: state.page,
                    item_per_page: state.item_per_page,
                    query: state.search,
                    order: state.order,
                }, false)
                .then(res => setState({
                    ...state,
                    list_product: res.data.list,
                    page: res.data.page,
                    total_page: res.data.total_page,
                    item_per_page: res.data.item_per_page,
                    total: res.data.total,
                    isLoading: false
                }))
        }
    }
    useEffect(() => {
        setState({...state, isLoading: true})
        get_data(BACK_END_URL + '/public/all_product',
            {
                page: state.page,
                item_per_page: state.item_per_page,
                query: state.search,
                order: state.order
            }, false)
            .then(res => setState({
                ...state,
                item_per_page: res.data.item_per_page,
                list_product: res.data.list,
                page: res.data.page,
                total_page: res.data.total_page,
                total: res.data.total,
                isLoading: false
            }))
    }, [state.order, state.item_per_page, state.page])
    const handleChange = (e) => {
        let name = e.target.name
        let value = e.target.value
        setState({
            ...state,
            [name]: value
        })
    }
    const handleChangePage = (event, newPage) => {
        setState({
            ...state,
            page: newPage + 1
        });
    };

    const handleChangeRowsPerPage = (event) => {
        setState({
            ...state,
            page: 1,
            item_per_page: event.target.value
        })
    };
    return (
        <Paper>
            <div>
                <Typography className={classes.allTable}>All</Typography>
                <Divider/>
                <div className={classes.searchContainer}>
                    <select value={state.order} className={classes.selectContainer} onChange={handleChange}
                            name='order'>
                        <option value={'name,asc'} disabled>Chose filter</option>
                        <option value={'name,asc'}>Name Asc</option>
                        <option value={'name,desc'}>Name Desc</option>
                        <option value={'count_click,asc'}>View Asc</option>
                        <option value={'count_click,desc'}>View desc</option>
                    </select>
                    <HtmlTooltip disableHoverListener arrow={true} placement="bottom-start"
                                 title={
                                     <React.Fragment>
                                         <Typography style={{fontSize: '0.7rem'}}>{`Press `}
                                             <div style={{
                                                 border: "1px solid #888a8c",
                                                 padding: '0 0.5rem 0 0.5rem',
                                                 borderRadius: '3px',
                                                 display: 'inline-block'
                                             }}>
                                                 <SubdirectoryArrowRightRoundedIcon
                                                     style={{fontSize: '0.7rem', paddingRight: '0.3rem'}}/>
                                                 Enter
                                             </div>
                                             {` to search`}</Typography>
                                     </React.Fragment>
                                 }
                    >
                        <Input className={classes.search} onKeyDown={handleKeyDown}
                               id="input-with-icon-textfield" disableUnderline
                               onChange={handleChange}
                               name="search" value={state.searchValue}
                               startAdornment={(<InputAdornment>
                                   <SearchRoundedIcon style={{paddingLeft: '0.5rem', paddingRight: '0.5rem'}}/>
                               </InputAdornment>)}
                        />
                    </HtmlTooltip>
                </div>
            </div>

            <TableContainer style={{maxHeight: 450, height: 450, display : 'flex', flexDirection : 'row', justifyContent : 'center'}}>
                {state.isLoading ?
                    <CircularProgress style={{marginTop : '8rem'}}/> :
                    <Table className={classes.table} stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow className={classes.headerTable}>
                                <TableCell align={'center'} style={{width: '5rem'}}><strong></strong></TableCell>
                                <TableCell align="left"><strong>Name</strong></TableCell>
                                <TableCell align="center"><strong>Price / Sale-Price </strong></TableCell>
                                <TableCell align="center"><strong>SubCategory</strong></TableCell>
                                <TableCell align="center"><strong>Category</strong></TableCell>
                                <TableCell align="center"><strong>Remain</strong></TableCell>
                                <TableCell align="center"><strong>New?</strong></TableCell>
                                <TableCell align="center"><strong>Hot?</strong></TableCell>
                                <TableCell align={'center'}><strong>Action</strong></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {state.list_product.map((row) => (
                                <TableRow key={row.product_id}>
                                    <TableCell align={'center'} style={{height: '20px'}}>
                                        <img src={row.url_image_main} className={classes.imageMain}/>
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        <NavLink to={`/edit_product/${row.product_id}`}>{row.name}</NavLink>
                                    </TableCell>
                                    <TableCell
                                        align="center">{`$ ${Number(row.price).toLocaleString('en')} / $ ${Number(row.sale_price).toLocaleString('en')}`}</TableCell>
                                    <TableCell align="center">{row.sub_category? row.sub_category.name : ''}</TableCell>
                                    <TableCell align="center">{row.category? row.category.name : ''}</TableCell>
                                    <TableCell align="center">{row.remain}</TableCell>
                                    <TableCell align="center"><IsNewComponent is_new={row.is_new?1:0} product_id={row.product_id} /> </TableCell>
                                    <TableCell align="center"><IsHotComponent is_hot={row.is_hot?1:0} product_id={row.product_id} /> </TableCell>
                                    <TableCell align="center">
                                        <div style={{
                                            display: "flex",
                                            flexDirection: "row",
                                            justifyContent: 'center'
                                        }}>
                                            <IconButton style={{color: '#e91e63'}} onClick={(e) => {
                                                setOpenDeleteDialog(true)
                                                setItemDelete(row.product_id)
                                            }}>
                                                <RestoreFromTrashIcon/>
                                            </IconButton>
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                }
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={state.total}
                rowsPerPage={state.item_per_page}
                page={state.page - 1}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
            <DeleteDialog open={openDeleteDialog} onClose={() => setOpenDeleteDialog(false)}
                          handleDelete={handleDeleteProduct}/>
        </Paper>
    );
}